body {
  background: -webkit-radial-gradient(
    center 80% circle,
    #6b527a 0,
    #4e3b58,
    #252033 100%
  );
  background: -o-radial-gradient(
    center 80% circle,
    #6b527a 0,
    #4e3b58,
    #252033 100%
  );
  background: radial-gradient(
    circle at center 80%,
    #6b527a 0,
    #4e3b58,
    #252033 100%
  );
  background-attachment: fixed;
}



